.Participants {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.container {
  width: 60%;
  margin: 1rem auto;
}

@media (max-width: 500px) {
  .container {
    width: 80%;
  }
}

@media (max-width: 350px) {
  .container {
    width: 100%;
  }
}

.DeletePopupContainer {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.DeletePopupEnter {
  opacity: 0;
  transform: scale(0.5);
}

.DeletePopupEnterActive {
  transform: scale(1);
  opacity: 1;
  transition: all 0.3s;
}

.DeletePopupExit {
  opacity: 1;
}

.DeletePopupExitActive {
  opacity: 0;
  transform: scale(0.8);
  transition: all 0.3s;
}
