.Hamburger,
.Hamburger::before,
.Hamburger::after {
  height: 2.5px;
  width: 100%;
  border-radius: 1px;
  background-color: white;
  position: absolute;
  transition: all 0.3s;
}

.Hamburger::before,
.Hamburger::after {
  content: "";
  display: inline-block;
}

.Hamburger::before {
  top: -7px;
}

.Hamburger::after {
  top: 7px;
}
.open {
  visibility: hidden;
}
.open::before {
  transform: translateY(7px) rotate(135deg);
  visibility: visible;
}
.open::after {
  transform: translateY(-7px) rotate(-135deg);
  visibility: visible;
}
