.container {
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: -5;
  background-image: radial-gradient(
      rgba(100, 148, 237, 0.95) 30%,
      rgba(100, 148, 237, 0.9) 30%,
      rgba(100, 148, 237, 0.8) 40%,
      rgba(100, 148, 237, 0.75) 40%,
      rgba(100, 148, 237, 0.6) 50%,
      rgba(100, 148, 237, 0.5) 50%,
      rgba(100, 148, 237, 0.4) 60%,
      transparent 60%
    ),
    url("background.jpg");
  background-size: cover;
  text-align: center;
  color: rgb(255, 242, 190);
  overflow: hidden;
}

.container p {
  animation: in-from-right 0.8s ease-out;
  max-width: 80%;
}

.heading {
  font-size: 4.8rem;
  animation: in-from-left 0.8s ease-out;
}

.title {
  font-size: 120%;
  color: rgb(255, 245, 104);
  font-weight: 600;
}

.Logo {
  width: 20rem;
  animation: in-from-top 0.8s ease-out;
}

@media (max-width: 500px) {
  .heading {
    font-size: 3.2rem;
  }

  .Logo {
    width: 10rem;
  }
}

@media (max-width: 350px) {
  .heading {
    font-size: 2.6rem;
  }
  .title {
    font-size: 4rem;
    display: block;
  }
}

@keyframes in-from-left {
  from {
    transform: translateX(-100rem);
  }
  to {
    transform: translateX(0rem);
  }
}

@keyframes in-from-right {
  from {
    transform: translateX(100rem);
  }
  to {
    transform: translateX(0rem);
  }
}

@keyframes in-from-top {
  from {
    transform: translateY(-100rem);
  }
  to {
    transform: translateY(0rem);
  }
}

@keyframes in-from-bottom {
  from {
    transform: translateY(100rem);
  }
  to {
    transform: translateY(0rem);
  }
}
