.Button {
  background-color: cornflowerblue;
  color: white;
  padding: 1rem 2rem;
  display: inline-block;
  width: fit-content;
  margin: 1.8rem 1rem;
  text-transform: uppercase;
  border-radius: 2rem;
  transition: all 0.2s ease-out;
  box-shadow: 1px 4px 3px rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.Button:hover {
  background-color: rgb(80, 117, 185);
  transform: translateY(-3px);
  box-shadow: 2px 7px 5px rgba(0, 0, 0, 0.4);
}

.Button:active {
  transform: translateY(-1px);
  box-shadow: 1px 5px 3px rgba(0, 0, 0, 0.4);
}

.Button2 {
  --color: cornflowerblue;
  color: var(--color);
  background-image: linear-gradient(to bottom, white 50%, var(--color) 50%);
  background-size: auto 250%;
  border: solid var(--color);
  border-width: 0 0 2px 0;
  margin: 1.2rem 1rem;
  padding: 0.7rem 1.5rem;
  display: inline-block;
  transition: all 0.2s ease-out;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  font-size: 1.4rem;
}
.Button2:hover {
  background-position: bottom;
  border-color: white;
  color: white;
  transform: translateY(-3px);
  box-shadow: 2px 5px 4px rgba(0, 0, 0, 0.4);
}

.Button2:active {
  transform: translateY(-1px);
  box-shadow: 1px 3px 3px rgba(0, 0, 0, 0.4);
}
