.Sidedrawer {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
  height: 100vh;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: cornflowerblue;
  transition: all 0.4s;
}

@media (min-width: 501px) {
  .Sidedrawer {
    display: none;
  }
}

.Sidedrawer nav {
  margin-top: 10%;
  width: 100%;
}

.Open {
  transform: translateX(0);
}

.Close {
  transform: translateX(-100%);
}

.Menu {
  position: fixed;
  left: 100%;
  width: 2.5rem;
  margin-top: 2.3rem;
  margin-left: 2.5%;
}

.Logo {
  width: 40%;
}

.Title {
  font-size: 3.6rem;
}
