.Participant {
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  padding: 1rem 1rem;
  border: 1px solid #eee;
  position: relative;
  text-align: left;
  background-color: white;
}

.Participant:first-of-type {
  font-weight: bold;
}

.Participant:first-of-type .DeleteStyle {
  visibility: hidden;
}

.DeleteStyle {
  flex: 0;
  opacity: 0;
  transition: all 0.2s;
  cursor: pointer;
}
@media (max-width: 500px) {
  .DeleteStyle {
    opacity: 1;
  }
}
.Participant:hover .DeleteStyle {
  opacity: 1;
}

.index {
  flex: 0.2;
}

.name {
  flex: 1;
}

@media (max-width: 500px) {
  .name {
    flex: 0.9;
  }
}
@media (max-width: 350px) {
  .name {
    flex: 0.7;
  }
}

.roll {
  flex: 0.3;
}
