.Toolbar {
  position: fixed;
  background-color: cornflowerblue;
  color: white;
  height: 5rem;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 49;
}

.Toolbar nav {
  display: inline-block;
  height: 100%;
}

.Logo {
  width: 5rem;
  height: 100%;
  display: inline-block;
}
@media (max-width: 500px) {
  .Toolbar {
    justify-content: flex-end;
  }

  .Toolbar nav {
    display: none;
  }
  .Logo {
    order: 2;
  }
  .title {
    order: 1;
    flex: 0.5;
  }
}

.title {
  font-size: 2.4rem;
  margin: auto 1rem auto 0.5rem;
}
