.DeleteButton::before,
.DeleteButton::after {
  content: "";
  height: var(--thickness);
  width: var(--size);
  transform: rotate(45deg);
  position: absolute;
  display: block;
  right: 0;
  position: relative;
  background-color: #555;
  top: calc((var(--size) - var(--thickness)) / 2);
  border-radius: 50rem;
}
.DeleteButton::after {
  transform: rotate(-45deg);
  top: calc((var(--size) - 3 * var(--thickness)) / 2);
}
.DeleteButton {
  --size: 2rem;
  --thickness: 0.2rem;
  height: var(--size);
}
