.Subject {
  width: 95%;
  margin: 2rem auto;
  border: 1px solid #777;
  border-radius: 2px;
  box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.5);
  height: 60vh;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  cursor: pointer;
  position: relative;
  border-width: 0;
}

.border {
  height: 1rem;
  width: 0;
  position: absolute;
  bottom: 0;
  background-color: cornflowerblue;
}

.Subject:hover .border {
  width: 100%;
  transition: width 0.3s;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image {
  width: 70%;
  height: 60%;
}
