.NavigationItem {
  height: 100%;
  position: relative;
}

.link:link,
.link:visited {
  color: white;
  text-decoration: none;
  height: 100%;
  display: inline-block;
  vertical-align: middle;
  padding: 1rem 3rem;
  transition: all 0.2s;
}
.link:hover {
  background-color: rgb(90, 132, 209);
}

.link:active,
.active {
  background-color: rgb(70, 104, 167);
}

@media (max-width: 500px) {
  .link:link,
  .link:visited {
    height: auto;
    display: inline-block;
    text-align: center;
    width: 100%;
    margin-top: 1rem;
    padding: 2rem 0;
    transition: all 0.2s;
  }
  .link:hover {
    background-color: rgb(90, 132, 209);
  }

  .link:active,
  .active {
    background-color: rgb(70, 104, 167);
  }
}
